.modal-fail {
  display: none;
  width: 320px;
  min-height: 280px;
  position: fixed;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15); }
  @media (min-width: 768px) {
    .modal-fail {
      width: 400px;
      min-height: 347px; } }

.modal-fail__wrapper {
  width: 237px;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .modal-fail__wrapper {
      width: 272px; } }

.modal-fail__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  padding-top: 40px;
  margin-bottom: 35px; }
  @media (min-width: 768px) {
    .modal-fail__title {
      font-size: 30px; } }

.modal-fail__text {
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
  margin-bottom: 32px; }

.modal-fail__button {
  border: none;
  margin: 0 auto;
  margin-bottom: 35px;
  width: 180px;
  height: 58px; }

.modal-fail__show {
  display: block;
  animation: bounce 0.5s; }

@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

